.checkbox-container {
    display: block;
    border: 3px solid var(--grey);
    position: relative;
    margin: 0 15px;
    margin-bottom: 2px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    user-select: none;
    font-family: "Mulish", sans-serif;
    font-size: 16px;
    color: #232323;
    font-weight: 700;
    margin-top: 4px;
    padding: 10px 55px 10px 15px;
    transition: 0.3s;
    border-radius: 10px;
    margin-left: 0px;
}

.checkbox-container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
    /* background-color: var(--grey) !important; */
}
.checkmark {
    position: absolute;
    top: 12px;
    right: 0px;
    left: 130px;
    height: 20px;
    width: 20px;
    background-color: var(--background);
    border: 0.5px solid #d3d3d3;
    border-radius: 10px;
    transition: 0.3s;
}
.checkmarkquiz {
    position: absolute;
    top: 12px;
    right: 0px;
    left: 130px;
    height: 25px;
    width: 25px;
    background-color: var(--background);
    border: 2.5px solid #d3d3d3;
    border-radius: 10px;
    transition: 0.3s;
}

.checkbox-container:hover input ~ .checkmark, .checkbox-container:hover input ~ .checkmarkquiz {
    background-color: var(--grey);
}
 
.checkbox-container input:checked ~ .checkmark , .checkbox-container input:checked ~ .checkmarkquiz {
    background-color: #6C63FF;
    border: 0.5px solid #6C63FF;
    transition: 0.3s;
}

.checkmark:after, .checkmarkquiz:after {
    content: "";
    position: absolute;
    display: none;
}
  
.checkbox-container input:checked ~ .checkmark:after, .checkbox-container input:checked ~ .checkmarkquiz:after {
    display: block;
}
.checkbox-container .checkmark:after{
    left: 6.5px;
    top: 3.5px;
    width: 5px;
    height: 9px;
    border: solid white;
    border-width: 0 2px 2px 0;
    border-radius: 0.1px;
    transform: rotate(45deg);
}
.checkbox-container .checkmarkquiz:after {
    left: 8px;
    top: 4px;
    width: 7.5px;
    height: 13px;
    border: solid white;
    border-width: 0 3px 3px 0;
    border-radius: 0.5px;
    transform: rotate(45deg);
}

