    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 20px 0px;
    letter-spacing: 0.2px;
}
.collapse-css-transition {
    transition: height 280ms cubic-bezier(0.4, 0, 0.2, 1);
}

.faq-title p{
    font-family: 'Poppins';
    font-size: 20px;
    margin: 0;
    color: var(--textColor);
    padding-top: 20px;
    font-weight: 600;
    padding: 0rem;
    letter-spacing: 0.3px;
}

.faq-content {
    background-color: var(--grey);
    
    padding: 15px;
    border-radius: 10px;
}

.faq-content p{
    font-family: 'Poppins';
    font-size: 16px;
    margin: 0;
    color: var(--sub);
    font-weight: 500;
    padding: 0rem;
    letter-spacing: 0.3px;
    line-height: 25px;
}

.mcqhover {
    cursor: pointer;
    border: 3px solid #ababab;
}
.mcqhover:hover {
    background-color: #6C63FF !important;
    transition: 0.2s ease;
}
.height{
    height: 60px !important;
}
.height2{
    height: 70% !important;
}
