.settings-icon {
	width: 35px;
	height: 35px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 40px;
	overflow: hidden;
	margin: 10px;
	transition: 0.2s ease;
	cursor: pointer;
	margin: 0 20px 0 20px;
}
.settings-icon:hover {
	background-color: var(--grey);
	/* opacity: 10%; */
	transition: 0.2s ease;
}
.settings-icon.seticon {
	z-index: 9999;
	opacity: 1;
}

