.toggle.react-toggle--focus .react-toggle-thumb {
	box-shadow: none;
}
.chartToggle.react-toggle--focus .react-toggle-thumb {
	box-shadow: none;
}

.toggle * {
	box-shadow: none;
}

.toggle.react-toggle--checked .react-toggle-track {
	background-color: #d3d3d3;
}
.chartToggle.react-toggle--checked .react-toggle-track {
	background-color: #6C63FF;
}
.toggle.react-toggle--checked .react-toggle-track:hover{
	background-color: #d3d3d3;
}

.toggle.react-toggle--checked:hover:not(.react-toggle--disabled) .react-toggle-track {
	background-color: #d3d3d3;
}

.toggle.react--checked:hover:not(.react-toggle--disabled) .react-toggle-track {
	background-color: #d3d3d3;
}

.toggle.react-toggle:active:not(.react-toggle--disabled) .react-toggle-thumb {
	box-shadow: none;
}

.chartToggle.react-toggle:active:not(.react-toggle--disabled) .react-toggle-thumb {
	box-shadow: none;
}

.toggle.react-toggle--checked .react-toggle-thumb {
	border: 2px solid #d3d3d3;
}

